* {
    font-family: 'Lato', sans-serif;
    color: $gray;
    box-sizing: border-box;
}

p {
    line-height: 1.45em;
}

a {
    text-decoration: none;
}

/* ============================================= */
/*              Typography                       */
/* ============================================= */

.headline-primary {
color: $logo-color1;
margin-left: 10px;
}

.headline-secondary {
font-weight: 300;
color: $gray;
}

.link {
color: $logo-color2;
font-weight: 700;
}

.blockquote {
font-size: 1.5em;
font-weight: 300;
font-style: italic;
margin-left: 20px;
padding-left: 20px;
border-left: 8px solid $logo-color1;
border-radius: 5px;
}